import React from 'react';
import ReactDOM from 'react-dom';
import * as utils from './utils.js';
import Card from './Card'

class DeckOfCards extends React.Component {

    constructor() {
        super();
        console.log('my m y', utils.newDeck());

        this.state = {
            deck: utils.newDeck()
        };
    }

    drop(ev) {
        ev.preventDefault();
        var data = ev.dataTransfer.getData("text");
        ev.target.appendChild(document.getElementById(data));
    }

    allowDrop(ev) {
        ev.preventDefault();
    }

    onDragStart(ev) {
        ev.dataTransfer.setData("text", ev.target.id);
    }

    returnCards(itr, data) {
        console.log(itr, data);
        return (<Card value={data}/>)
    }

    render() {
        return (
            <div className="main-container">
                <div className="child-30">
                    <div className="div-by-4">
                        <div className="card-holder" id="spades-holder">
                            <div className="empty-holder-b"><p className="empty-text">Place spades here</p>
                                <p>&spades;</p></div>
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="clubs-holder">
                            <div className="empty-holder-b"><p className="empty-text">Place clubs here</p>
                                <p>&clubs;</p></div>
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="hearts-holder">
                            <div className="empty-holder-r"><p className="empty-text">Place hearts here</p>
                                <p>&hearts;</p></div>
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="diamonds-holder">
                            <div className="empty-holder-r"><p className="empty-text">Place diamonds here</p>
                                <p>&diams;</p></div>
                        </div>
                    </div>

                </div>
                <div className="child-70">
                    {
                        this.state.deck.map(this.returnCards)
                    }
                </div>

                {/*<div style={{'width': 200, 'height': 200}} id="div1" onDrop={this.drop} onDragOver={this.allowDrop}>*/}
                {/*<p draggable="true" onDragStart={this.onDragStart} id="drag1" width="88"*/}
                {/*height="31">Hello</p>*/}
                {/*</div>*/}

                {/*<div style={{'width': 200, 'height': 200}} id="div2" onDrop={this.drop} onDragOver={this.allowDrop}></div>*/}
            </div>
        )
    }

}

ReactDOM.render(
    <DeckOfCards/>
    , document.getElementById('app'))


